<template>
  <div class="requirements">
    <ul class="holdables__container tree-view">
      <b>Требования</b>
      <ul v-if="extraRequirements?.requirementsArray.length > 0">
        <li>{{ extraRequirements.name }}</li>
        <ul>
          <li
            v-for="(item, index) in extraRequirements.requirementsArray"
            :key="componentId + index + 'extra'"
          >
            {{ item }}
          </li>
        </ul>
      </ul>
      <ul v-if="skills">
        <li><i class="ra ra-wrench" /> Навыки</li>
        <ul>
          <li
            v-for="(item, index) in filterZeroValues(Object.entries(skills))"
            :key="componentId + index + 'skill'"
          >
            {{ skillsAndKnowledge[item[0]] }}: {{ item[1] }}
          </li>
        </ul>
      </ul>
      <ul v-if="attributes">
        <li><i class="ra ra-muscle-up" /> Атрибуты</li>
        <ul>
          <li
            v-for="(item, index) in filterZeroValues(
              Object.entries(attributes)
            )"
            :key="componentId + index + 'attr'"
          >
            {{ attributesEnum[item[0]] }}: {{ item[1] }}
          </li>
        </ul>
      </ul>
      <!--TODO: добавить отображение тэгов-->
      <ul v-if="tags">
        <li><i class="ra ra-cog" /> Тэги</li>
        <ul>
          <li>///в разработке</li>
        </ul>
      </ul>
      <ul v-if="stopTags">
        <li><i class="ra ra-anchor" /> Запрещающие тэги</li>
        <ul>
          <li>///в разработке</li>
        </ul>
      </ul>
    </ul>
  </div>
</template>

<script>
import skillsAndKnowledge from "@/enums/skillsEnum";
import attributesEnum from "@/enums/attributes";
import { uniqueString } from "@/helpers/helpersList";

export default {
  name: "Requirements",
  data() {
    return {
      componentId: "Requirements" + uniqueString(),
      skillsAndKnowledge,
      attributesEnum,
    };
  },
  props: ["skills", "attributes", "tags", "stopTags", "extraRequirements"],
  methods: {
    filterZeroValues(array) {
      return array.filter((item) => item[1] !== 0);
    },
  },
};
</script>

<style lang="scss">
.requirements {
  font-size: 0.8em;
  height: calc(100% - 10px);

  .tree-view {
    height: 100%;
  }
}
</style>
