<template>
  <div
    class="ability-popup popup-regular"
    :style="!isLoaded && 'cursor: progress'"
  >
    <template v-if="isLoaded">
      <div class="title d-flex justify-content-between align-items-center">
        <div class="d-flex align-items-center">
          <i class="ra ra-blaster ra-2x me-1" />
          {{ abilityType[abilityModel.ability_type].toUpperCase() }}:
          {{ abilityModel.name }}
        </div>
        <div class="roll-stat clear-bg bordered">
          <i class="ra ra-wrench me-1" />
          {{ skillsAndKnowledge[abilityModel.roll_stat] }}
        </div>
      </div>
      <div class="d-flex panel p-2 mb-3">
        <div class="stat">
          Стамина:
          <div class="stat__value ms-1" style="color: #6b7742">
            {{ abilityModel.consume_stamina }}
          </div>
        </div>
        <div class="stat">
          Фокус:
          <div class="stat__value ms-1" style="color: #4e4277">
            {{ abilityModel.consume_focus }}
          </div>
        </div>
        <div class="stat">
          ОД:
          <div class="stat__value ms-1" style="color: #426b77">
            {{ abilityModel.consume_od }}
          </div>
        </div>
        <div class="stat" style="margin-left: auto">
          Откат:
          <div class="stat__value ms-1">
            {{ abilityModel.cooldown }}<i class="ra ra-clockwork" />
          </div>
        </div>
      </div>
      <div class="row">
        <div class="col">
          <Requirements
            :attributes="abilityModel.attr_req"
            :skills="abilityModel.skill_req"
            :tags="abilityModel.tag_req"
            :stop-tags="abilityModel.tag_stop"
          />
        </div>
        <div class="col">
          <div class="clear-bg bordered p-2 mb-2">
            <b>Тэги способности:</b>
            <!--TODO: добавить отображение тэгов-->
            //В разработке
          </div>
          <div class="clear-bg bordered p-2">
            <p class="m-0 mb-2">
              <b>Цель: </b> {{ targetTypes[abilityModel.target_type] }}
              {{ abilityModel.locational ? "(по части тела)" : "" }}
            </p>
            <p
              class="m-0 mb-2"
              v-if="
                abilityModel.min_distance !== 0 &&
                abilityModel.max_distance !== 0
              "
            >
              <b>Дистанция: </b> {{ abilityModel.min_distance }} -
              {{ abilityModel.max_distance }}
            </p>
            <p class="m-0 mb-2">
              <b>Травмоопасность: </b>
              {{ abilityModel.traumatic ? "Присутствует" : "Отсутствует" }}
            </p>
            {{ abilityModel.description }}
          </div>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
import { createRequest } from "@/api/requestBuilder";
import requestConfigs from "@/api/requestConfigs";
import abilityType from "@/enums/abilityType";
import skillsAndKnowledge from "@/enums/skillsEnum";
import targetTypes from "@/enums/utilConstants/targetTypes";
import Requirements from "@/components/Statblocks/Requirements.vue";

export default {
  name: "AbilityPopup",
  components: { Requirements },
  data() {
    return {
      abilityModel: {},
      isLoaded: false,
      abilityType,
      skillsAndKnowledge,
      targetTypes,
    };
  },
  props: {
    popupData: {
      type: Object,
    },
  },
  beforeMount() {
    createRequest(
      this.popupData.params.isDefensive
        ? requestConfigs.GETAbilityDefensive
        : requestConfigs.GETAbility,
      {
        queryPayload: {
          characterName: this.$store.state.selectedCharacterName,
          abilityName: this.popupData.params.id,
        },
      }
    ).then((result) => {
      this.abilityModel = result.data.ability;
      this.isLoaded = true;
    });
  },
};
</script>

<style lang="scss">
.ability-popup {
  .title {
    font-family: "Press Start 2P", cursive;
    margin-bottom: 13px;
  }
  .roll-stat {
    color: #6b7742;
    font-size: 0.8em;
    display: flex;
    align-items: center;
    padding: 8px;
  }
  .stat {
    font-weight: bold;
    display: flex;
    align-items: center;
    margin-right: 15px;

    &__value {
      display: inline-block;
      padding: 4px;
      font-family: "Press Start 2P", cursive;
      background-color: white;
      border: 2px;
    }
  }
}
</style>
